.overlay {
  position: absolute;
  left: 0;
  right: 0;
  width: 100% ;
  height:  100%;
  background: rgba(0, 0, 0, 0.35);
  overflow: auto;
}
.cart {
  display: grid;
  grid-template-rows: 0.5fr 6fr 1fr;
  position: absolute;
  width: 614px;
  right: 0;
  height: fit-content;
  background: white;
  box-shadow: -10px 0px 20px #00000080;
  
  /* margin: 0px 50px 0px 0px; */
}
.title_block {
  display: grid;
  grid-template-columns: 1fr 0fr;
  height: 66px;
  width: 100%;

  background: #121212;
}

.title_block h2 {
  align-self: center;
  justify-self: center;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 21px;

  text-align: center;
  text-transform: uppercase;

  color: #ffffff;
}

.close_btn {
  width: fit-content;
  justify-self: end;
  align-self: center;
  border: 2px solid rgba(18, 18, 18, 0.65) !important;
  background-color: #121212;
  color: #ffff;
}
.total_price {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1.3fr 2fr;
  align-items: end;
}

.total_price button {
  grid-row: 2/3;
  grid-column: 1/3;
  align-self: center;
  height: fit-content;
  background: linear-gradient(92.96deg, #7a0000 0%, #990000 100%);
  color: white;
  padding: 20px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
}

.total_price p {
  font-size: 20px;
  font-weight: bold;
  padding-left:50px;
}

.total_price_summ {
  justify-self: end;
}

.cart_list {
  overflow: auto;
}

.cart_item {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  border: 1px none #9D9D9D;
  border-radius: 15px;
  margin-bottom: 30px;
  padding: 15px 50px 0px 50px;
  
}

.cart_img {
  max-width: 36% !important;
    height: auto !important;
}
.close_btn_item {
    justify-self: end;
    align-self: baseline;
    border: none;
    outline: none;
  }

  .cart_price{
    justify-self: end;
    align-self: baseline;

  }

  @media (min-width: 375px) and (max-width: 600px) {
    /*sm*/
    
  
        .overlay {
           position: inherit;
          left: 0;
          right: 0;
          width: 40% ;
          height:  50%;
          background: rgba(0, 0, 0, 0.35);
          overflow: auto;

        }
        .cart {
          display: grid;
          grid-template-rows: 0.5fr 6fr 1fr;
          position: absolute;
          width: auto;
          right: 0;
          height: fit-content;
      
        }
      
      }