.search{
  display: grid;
  grid-template-columns: 2fr 1fr;
  align-items: center;
}

.search h2 {
  justify-self: start;
  font-size: 32px;
}

.search_block{
  justify-self: end;
  border: 1px solid rgba(122, 0, 0, 0.35);
  border-radius: 7px;
  padding: 15px;
  width: 250px;
  margin: auto;

}


.search_block input {
  border: none;
  font-size: 14px;
}

.search_block input:focus-visible {
  outline: none;
  border-bottom: none;
}
.favorite-btn{
  display: inline-block;
  position: relative;
  font-size: 15px;
border: none;  
padding-right: 40px;
}
.favorite-btn::before{
  font-size: 3em;
  color: #000;
  content: '♥';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.favorite-btn::after{
  font-size: 3em;
    color: red;
    content: '♥';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) scale(0);
    transition: transform 0.2s;
}
.favorite-btn.liked::after{
  transform: translate(-50%, -50%) scale(1.1);

}

.favour{
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
 
}

.wine-btn_clear{
  background: linear-gradient(92.96deg, #7A0000 0%, #990000 100%) !important;
border-radius: 3px !important;
color: #E6E3C4 !important;


}.wine-btn_clear::before{
  content: "Купить" !important;
